body {
  background: rgb(65, 65, 255);
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  margin: 0;
}

/* Container adapts to screen size */
.container {
  background: white;
  border: 1px solid #ddd;
  border-radius: 20px;
  width: 100%;
  max-width: 600px; /* limit width on large screens */
  padding: 1rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
  transition: box-shadow 0.2s ease, transform 0.2s ease;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

/* Header */
.header {
  border-bottom: 1px solid #ddd;
  padding-bottom: 0.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

#mainHeading {
  font-size: clamp(1.2rem, 4vw, 1.8rem);
  margin: 0;
}

#setTiming {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: lightblue;
  border-radius: 8px;
  padding: 0.25rem 0.5rem;
  font-size: 0.9rem;
}

#timeText {
  margin: 0;
}

#timing {
  background: black;
  color: white;
  border-radius: 6px;
  padding: 2px 6px;
  min-width: 24px;
  text-align: center;
}

/* Question controls */
#nextQuestion {
  background: lightblue;
  padding: 0.5rem 1rem;
  border-radius: 8px;
  margin-left: auto;
  cursor: pointer;
}

#questionNumber {
  font-weight: bold;
}

/* Main content */
.main {
  margin-top: 1rem;
}

#options {
  list-style-type: none;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

#options li {
  margin: 0;
}

/* Footer */
.footer {
  margin-top: auto;
  padding-top: 1rem;
  text-align: center;
}

/* ðŸ“± Responsive tweaks */
@media (max-width: 768px) {
  .container {
    padding: 0.75rem;
    border-radius: 15px;
  }

  #nextQuestion {
    margin-top: 0.5rem;
    width: 100%;
    justify-content: center;
  }

  #setTiming {
    margin-top: 0.5rem;
  }
}

@media (max-width: 480px) {
  #mainHeading {
    font-size: 1.2rem;
    text-align: center;
    width: 100%;
  }

  .header {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }

  #setTiming {
    width: 100%;
    justify-content: center;
  }

  #nextQuestion {
    width: 100%;
    text-align: center;
  }
}